import { Color } from "three";

export function hex_to_color(hex_code: string) {
  return new Color(hex_code.length === 6 ? parseInt('0x' + hex_code, 16) : hex_code);
}

export const getFormatedColor = (color: string) => {
  if (!color.length) return '';

  const r = /#/g;
  return '#' + color.replace(r, '');
}

export const getSvgIconHTML = (icon: string) => {
  let svg;
  const svg_block = document.getElementById(`map-special-svg-${icon}`);
  if (svg_block) {
    const svg_element = svg_block.querySelector('svg');
    if (svg_element) {
      const svgColoredParts = svg_element.querySelectorAll('[fill]');
      svgColoredParts.forEach((svgColoredPart) => {
        svgColoredPart.setAttribute('fill', '#FFFFFF');
      });
      svg = (new XMLSerializer()).serializeToString(svg_element);
    }
  }
  return svg;
}