import { useLazyGetRetailersQuery } from "src/services/retailersApi"
import { useCallback, useState } from "react";
import { IRetailer } from "src/mapitApiTypes";

const useAllRetailers = (baseUrl: string, center_id: string, limit: number) => {
  const [retailers, setRetailers] = useState<IRetailer[]>([])
  const baseQuery = baseUrl?.lastIndexOf('/') === baseUrl.length - 1 ? baseUrl.slice(0, -1) : baseUrl;

  const [getRetailers] = useLazyGetRetailersQuery()

  const getAllRetailers = useCallback(() => new Promise<IRetailer[]>((resolve, reject) => {
    let items: IRetailer[] = [];

    const fetchData = (currentPage: number = 1) => {
      getRetailers({ baseQuery, center_id, limit, page: currentPage }, true).then((response: any) => {
        if (response?.data?.items) {
          items = [...items, ...response.data.items];
          if (response.data.count > items.length) {
            fetchData(currentPage + 1);
          } else {
            setRetailers(items);
            resolve(items);
          }
        }
      })
        .catch((err) => {
          console.error('Error fetching retailers', err);
          resolve([]);
        });
    };

    fetchData();
  }), [baseQuery, center_id, getRetailers, limit])

  return { retailers, getAllRetailers }
}

export default useAllRetailers;